import React from 'react';
import ValBtn from '../ValButton';
import {IoIosBeer} from 'react-icons/io';
import { Col, Row } from 'react-bootstrap';
import {HitCounter} from './HiCounter';
import './styles.css';

export const BeerComponent = () => {
    return(
        <Row>
        <Col xs={12} md='auto'>
            <Row className='valbtn-cont mb-3 justify-content-center justify-content-md-start'>
                <ValBtn text='say hi 👋'/>

            </Row>
        </Col>
        <Col xs={12} md='auto' className='align-self-center'>
            <Row className='align-items-center mb-3 mx-4 justify-content-center justify-content-md-start'>
                <HitCounter class='hi-counter' />
                &nbsp;&nbsp;&nbsp;
                <IoIosBeer color='white' size={40}/>
            </Row>
        </Col>
    </Row>
    )
}