import firebase from 'firebase';
import React, { useEffect, useRef, useState } from 'react';
import { Spinner } from 'react-bootstrap';
import firebaseInstance from '../../../Firebase/firebase';

interface IProps{
    class?: string
}

export const HitCounter = (props: IProps) =>{
    const [isLoading, setLoading] = useState(true);
    const [hits, setHits] = useState(0);
    const watcher = useRef<null| firebase.database.Reference>(null);

    
    useEffect(() => {
        watcher.current = firebaseInstance.attachHitsWatcher();
        watcher.current.on('value', ss => {            
            setHits(ss.val());
            setLoading(false);
        });
    }, [])

    if(isLoading){
        return(
            <div className={props.class}>
                <Spinner variant='light' animation='grow'/>
            </div>
        )
    }

    return(
        <div className={props.class}>{hits}</div>
    )
}